import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const STORAGE_KEY = "RandomPicker!@#76510";
const LIST_KEY = STORAGE_KEY + "_list";
const ACTIVE_LIST_KEY = STORAGE_KEY + "_activeList";

export default new Vuex.Store({
  state: {
    activeList: 0,

    list: [
      {
        id: 0,
        text: "Yes / No?",
        options: [
          {
            id: 0,
            text: "Yes",
            color: "green",
          },
          {
            id: 1,
            text: "No",
            color: "red",
          },
        ],
      },
      {
        id: 1,
        text: "What's for dinner?",
        options: [
          {
            id: 0,
            text: "Chicken",
            color: "orange",
          },
          {
            id: 1,
            text: "Mince",
            color: "pink",
          },
          {
            id: 2,
            text: "Steak",
            color: "purple",
          },
          {
            id: 3,
            text: "Salad",
            color: "darkblue",
          },
          {
            id: 4,
            text: "Pizza",
            color: "blue",
          },
          {
            id: 5,
            text: "Burgers",
            color: "skyblue",
          },
        ],
      },
    ],
  },

  mutations: {
    changeActiveList: function(state, index) {
      state.activeList = state.list[index];
      this.commit("updateStorage", false);
    },

    updateList: function(state, item) {
      if (item.id === null) {
        // add new list
        let id = 0;
        if (state.list.length) {
          id = state.list[state.list.length - 1].id + 1;
        }

        state.list.push({ ...item, id });
      } else {
        // update exist list name
        const index = state.list.findIndex(
          (listItem) => listItem.id == item.id
        );

        if (index > -1) {
          state.list.splice(index, 1, item);
          if (state.activeList.id == item.id) {
            this.commit("changeActiveList", index);
          }
        }
      }

      this.commit("updateStorage", true);
    },

    updateOptions: function(state, { item, option }) {
      if (option.id === null) {
        // add new list
        let id = 0;
        if (item.options.length) {
          id = item.options[item.options.length - 1].id + 1;
        }

        item.options.push({ text: option.text, color: option.color, id });
      } else {
        // update exist list name
        const index = item.options.findIndex(
          (optionItem) => optionItem.id == option.id
        );

        if (index > -1) {
          item.options.splice(index, 1, option);
        }
      }

      this.commit("updateList", item);
    },

    removeListItem: function(state, item) {
      const index = state.list.indexOf(item);

      if (index > -1) {
        state.list.splice(index, 1);
        if (state.activeList.id == item.id) {
          this.commit("changeActiveList", 0);
        }
        this.commit("updateStorage", true);
      }
    },

    removeOption: function(state, { item, option }) {
      const index = state.list.indexOf(item);

      if (index > -1) {
        const optionIndex = state.list[index].options.indexOf(option);
        if (optionIndex > -1) {
          state.list[index].options.splice(optionIndex, 1);
          if (state.activeList.id == item.id) {
            this.commit("changeActiveList", index);
          }
          this.commit("updateStorage", true);
        }
      }
    },

    updateStorage: (state, isList) => {
      if (isList) {
        window.localStorage.setItem(
          LIST_KEY,
          JSON.stringify({ data: state.list })
        );
      } else {
        window.localStorage.setItem(
          ACTIVE_LIST_KEY,
          JSON.stringify({ data: state.activeList })
        );
      }
    },

    loadStorage: (state) => {
      const activeList = JSON.parse(
        window.localStorage.getItem(ACTIVE_LIST_KEY)
      );
      if (activeList) {
        state.activeList = activeList.data;
      }

      const list = JSON.parse(window.localStorage.getItem(LIST_KEY));
      if (list) {
        state.list = list.data;
      }
    },
  },

  getters: {
    listData: (state) => {
      return (id) => {
        return state.list.find((listItem) => listItem.id == id);
      };
    },
  },

  actions: {},
  modules: {},
});
