import Vue from "vue";
import VueRouter from "vue-router";
const Home = () => import("../views/home.vue");
const List = () => import("../views/list.vue");
const Items = () => import("../views/items.vue");

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/list",
    name: "List",
    component: List,
  },
  {
    path: "/items/:id",
    name: "Items",
    props: true,
    component: Items,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
